.header {
  display: flex;
  /* background-color: rgb(191, 102, 102); */
  background-color: rgb(212, 65, 65);
  width: 100vw;
  height: 8vh;
  opacity: 0.95;
  align-items: center;
  justify-content: space-evenly;
  padding: 0 ! important;
}

.header:hover {
  opacity: 1;
}

.navbar-nav {
  height: 8vh;
  align-items: center;
}

.navbar-expand-lg .navbar-collapse {
  display: flex;
  justify-content: space-evenly;
}

.container {
  width: 100vw;
}

.container-fluid {
  display: flex;
  background-color: rgb(212, 65, 65);
  top: 0;
  height: 8vh;
  justify-content: space-evenly;
}

button {
  background-color: rgb(212, 65, 65);
  text-decoration: none !important;
  border: none;
  color: aliceblue;
  font-family: monospace, sans-serif !important;
  font-size: 1vw !important;
}

.main-div {
  height: 100vh;
}

.footer h6 {
  align-items: center;
  background-color: rgb(212, 65, 65);
  color: aliceblue;
  display: flex;    
  font-family: monospace, sans-serif !important;
  font-size: 1vw !important;
  justify-content: center;
  height: 5vh;
  margin: 0;
  width: 100vw;
}

.footer h6:hover {
  opacity: 1;
}
